.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

@charset "UTF-8";
.formio-loader {
  position: relative;
  min-height: 60px;
}

.loader-wrapper {
  z-index: 1000;
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  height: 120px;
  background-color: rgba(0, 0, 0, 0);
}

.loader {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -30px;
  margin-top: -30px;
  z-index: 10000;
  display: inline-block;
  border: 6px solid #f3f3f3; /* Light grey */
  border-top: 6px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.formio-form {
  position: relative;
  min-height: 80px;
}

.formio-error-wrapper,
.formio-warning-wrapper {
  padding: 1em;
}

.formio-error-wrapper {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}
.formio-error-wrapper .formio-errors .error {
  color: #C20000;
}
.formio-error-wrapper .field-required:after {
  color: #C20000;
}

.formio-warning-wrapper {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}

.formio-disabled-input .form-control.flatpickr-input {
  background-color: #eee;
}

.builder-component.has-error .invalid-feedback,
.formio-component.has-error .invalid-feedback,
.formio-component.alert-danger .invalid-feedback,
.formio-component.has-message .invalid-feedback,
.formio-component-modal-wrapper.has-message .invalid-feedback,
.formio-component-modal-wrapper.has-error .invalid-feedback {
  display: block;
  color: inherit;
  margin-top: 4px;
}

.formio-errors .error {
